import global from '../../global'
import router from '../../router'
import http from '../..//http';

var config = global.getAxiosConfig(global.locale);


const b64DecodeUnicode = function b64DecodeUnicode(str) {
    return decodeURIComponent(Array.prototype.map.call(atob(str), function (c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2)
    }).join(''))
};

const parseToken = function (accessToken) {
    // console.log('accessToken:', accessToken);
    if (accessToken) {
        try {
            let base64Url = accessToken.split('.')[1];
            let base64 = base64Url.replace('-', '+').replace('_', '/');
            //console.log('base64', base64);
            //console.log(b64DecodeUnicode(base64));
            return JSON.parse(b64DecodeUnicode(base64));
        } catch (e) {
            console.log('accessToken parsing error: ', e);
            console.log('accessToken: ', accessToken);
            return undefined;
        }

    } else {
        return undefined;
    }
};


const state = {
    accessToken: '',
    //회원가입용 정보.
    tokenInfo: parseToken(localStorage.getItem('accessToken')),
    member: {
        idx: 0,
        groupIdx: 0,
        fileIdx: 0,
        year: "",
        nickName: "",
        businessYn: "",
        businessName: "",
        businessContent: "",
        businessCategoryIdx: "",
        businessSubCategoryIdx: 0,
        businessEmail: "",
        businessTel: "",
        businessSite: "",
        businessSdate: "",
        businessEdate: "",
        adminCheckYn: "",
        emailCertYn: "",
        userStatus: "",
        createAt: "",
        updateAt: ""
    },
    account: {
        idx: 0,
        accountType: "",
        deviceType: "",
        email: "",
        pushToken: "",
        pushYn: "N",
        snsToken: "",
        snsType: "",
        userIdx: 0,
        userPwd: "",
        createAt: "",
        updateAt: ""
    },
};

const getters = {
    //Member
    getIdx: function () {
        return state.member.idx;
    },
    getGroupIdx: function () {
        return state.member.groupIdx;
    },
    getFileIdx: function () {
        return state.member.fileIdx;
    },
    getYear: function () {
        return state.member.year;
    },
    getNickName: function () {
        return state.member.nickName;
    },
    getBusinessYn: function () {
        return state.member.businessYn;
    },
    getBusinessName: function () {
        return state.member.businessName;
    },
    getBusinessContent: function () {
        return state.member.businessContent;
    },
    getBusinessCategoryIdx: function () {
        return state.member.businessCategoryIdx;
    },
    getBusinessSubCategoryIdx: function () {
        return state.member.businessSubCategoryIdx;
    },
    getBusinessEmail: function () {
        return state.member.businessEmail;
    },
    getBusinessTel: function () {
        return state.member.businessTel;
    },
    getBusinessSite: function () {
        return state.member.businessSite;
    },
    getBusinessSdate: function () {
        return state.member.businessSdate;
    },
    getBusinessEdate: function () {
        return state.member.businessEdate;
    },
    getUserStatus: function () {
        return state.member.userStatus;
    },
    getCreateAt: function () {
        return state.member.createAt;
    },
    getUpdateAt: function () {
        return state.member.updateAt;
    },

    //Account
    getEmail: function () {
        return state.account.email;
    },
    getPassword: function () {
        return state.account.userPwd;
    },

    getPushToken: function () {
        return state.account.pushToken;
    },
    getPushYn: function () {
        return state.account.pushYn;
    },
    getAccountIdx: function () {
        return state.account.idx;
    },
    getAccountType: function () {
        return state.account.accountType;
    },
    getSnsToken: function () {
        return state.account.snsToken;
    },
    getSnsType: function () {
        return state.account.snsType;
    },
    getUserIdx: function () {
        return state.account.userIdx;
    },
};


const mutations = {
    login(state, {accessToken, deviceType, accountType, midx, aidx}) {
        // console.log("loginIdx ===",midx);
        state.accessToken = accessToken;
        state.account.idx = aidx;
        state.account.userIdx = midx;
        state.member.idx = midx;
        state.account.deviceType = deviceType;
        localStorage.accessToken = accessToken;
        localStorage.setItem('accessToken', accessToken);
        state.tokenInfo = parseToken(accessToken);
    },
    logout(state) {
        state.accessToken = '';
        delete localStorage.accessToken;
        state.tokenInfo = global.domain.tokenInfo;
        // 모든 HTTP 요청헤더에 Authorization을 추가
        //http.createAxios().defaults.headers.common['Authorization'] = undefined;
        router.push({name: 'Intro'});
    },
    setTokenUserIdx: function (state, payload) {
        state.tokenInfo.idx = payload;
    },
    setMemberName: function (state, payload) {
        return state.member.nickName = payload;
    },

};

const actions = {
    login({commit}, {email, password, deviceType}) {
        return http.createAxios().post(global.baseURI + global.api.post.member.signin, {
            'email': email,
            'password': password,
            'deviceType': deviceType
        }, config)
            .then(({datas}) => {
                // console.log(datas);
                commit('login', datas);
            }).catch((response) => {
            });
    },
    logout({commit}) {
        commit('logout');
    },
    apiGetUsersDataAct: function (context, payload) {
        return http.createAxios().post(global.baseURI + global.privateUri + global.api.post.member.findMember, payload, config).then((response) => {
            var data = response.data.datas;
            state.member = data;
        }).catch(response => {
            console.log(response);
        });
    },
    apiJoinUserAct: function (context, payload) {
        return new Promise((resolve, reject) => {
            http.createAxios().post(global.baseURI + global.api.post.member.signUp, payload, config)
                .then(response => {
                    console.log("response:", response.data);
                    if (response.data.code == "OK") {
                        context.commit('login', response.data.returnData);
                    } else {
                        //오류시
                        context.commit("setErrorCode", response.data);
                    }
                    resolve(response.data);
                })
                .catch(error => {
                    console.log(error.response);
                    reject(error.response)
                });
        })
    },
    checkUserNickName: function (context, payload) {
        return new Promise((resolve, reject) => {
            return http.createAxios().post(global.baseURI + global.publicUri + global.api.post.member.checkNickName, payload, config).then((response) => {
                resolve(response.data);
            }).catch(response => {
                reject(response);
            });
        });
    },
    saveUserYear: function (context, payload) {
        return new Promise((resolve, reject) => {
            return http.createAxios().post(global.baseURI + global.privateUri + global.api.post.member.saveYear, payload, config).then((response) => {
                resolve(response.data);
            }).catch(response => {
                reject(response);
            });
        });
    }
};


export default {
    state,
    getters,
    actions,
    mutations
}



